.card {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.arrowButton {
  font-size: 20px;
  background-color: #e3db6a;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.arrowButton:hover {
  background-color: #d0d0d0;
}

.word-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(197, 167, 114);
  width: 500px;
  height: 50vh;
  border-radius: 10px;
  margin: 0 10px;
}

.word-card h3 {
  margin-bottom: 20px;
}

.word-card button {
  font-size: 16px;
  background-color: #d6e4a3;
  border: none;
  padding: 8px 12px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

.word-card button:hover {
  background-color: #d0d0d0;
}

